.ProseMirror {
  position: relative;
}

.ProseMirror {
  word-wrap: break-word;
  white-space: pre-wrap;
  white-space: break-spaces;
  font-variant-ligatures: none;
  font-feature-settings: "liga" 0; /* the above doesn't seem to work in Edge */
}

.ProseMirror pre {
  white-space: pre-wrap;
}

.ProseMirror li {
  position: relative;
}

.ProseMirror-hideselection *::-moz-selection { background: transparent; }

.ProseMirror-hideselection *::selection { background: transparent; }

.ProseMirror-hideselection *::-moz-selection { background: transparent; }

.ProseMirror-hideselection { caret-color: transparent; }

.ProseMirror-selectednode {
  outline: 2px solid #8cf;
}

/* Make sure li selections wrap around markers */

li.ProseMirror-selectednode {
  outline: none;
}

li.ProseMirror-selectednode:after {
  content: "";
  position: absolute;
  left: -32px;
  right: -2px; top: -2px; bottom: -2px;
  border: 2px solid #8cf;
  pointer-events: none;
}

.milkdown .ProseMirror-selectednode {
        outline: 2px solid rgba(var(--line), 1);
    }

.milkdown li.ProseMirror-selectednode {
        outline: none;
    }

.milkdown li.ProseMirror-selectednode::after {
        border: 2px solid rgba(var(--line), 1);
    }

:root {
    --font: Roboto, HelveticaNeue-Light, Helvetica Neue Light, Helvetica Neue, Helvetica, Arial, Lucida Grande, sans-serif;
    --font-code: Consolas, Monaco, Andale Mono, Ubuntu Mono, monospace;
    --radius: 4px;
    --shadow: 59, 66, 82;
    --primary: 94, 129, 172;
    --secondary: 129, 161, 193;
    --neutral: 46, 52, 64;
    --solid: 76, 86, 106;
    --line: 216, 222, 233;
    --background: 236, 239, 244;
    --surface: 255, 255, 255;
}

[data-theme='dark'] {
    --font: Roboto, HelveticaNeue-Light, Helvetica Neue Light, Helvetica Neue, Helvetica, Arial, Lucida Grande, sans-serif;
    --font-code: Consolas, Monaco, Andale Mono, Ubuntu Mono, monospace;
    --radius: 4px;
    --shadow: 59, 66, 82;
    --primary: 94, 129, 172;
    --secondary: 129, 161, 193;
    --neutral: 236, 239, 244;
    --solid: 216, 222, 233;
    --line: 67, 76, 94;
    --background: 37, 41, 50;
    --surface: 46, 52, 64;
}
